
.goods-main {
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  top: 170px;
  bottom: 47px;
  position: absolute;
}
.goods-main .goods-wapper {
  -ms-flex: 0 0 100px;
      flex: 0 0 100px;
  height: 100%;
  overflow-y: scroll;
  background-color: #f3f5f7;
}
.type-box{
  list-style: none;
}
.type-box .type-item{
  display: block;
  background-color: #f3f5f7;
  padding: 17px 20px;
}
.type-box .selected {
  background-color: #ffffff;
}
.type-box .type-item a{
  font-size: 12px;
  color: rgb(121, 124, 128);
}
.goods-main .goods-list{
  overflow-y: scroll ;
  -ms-flex: 1;
      flex: 1;
}
.goods-item h3{
  font-size: 18px;
  color: rgb(147,153,159);
  padding: 10px 0px 10px 18px;
  background-color: #f3f5f7;
  margin-bottom: 18px;
}
.foods-list{
  list-style: none;
}
.foods-list li{
  display: -ms-flexbox;
  display: flex;
  margin: 0px 18px 18px 18px;
  border-bottom: 1px solid #7e8c8d;
  padding-bottom: 18px;
}
.foods-list li:last-child{
  border: none;
}
.food-img {
  -ms-flex: 0 0 80px;
      flex: 0 0 80px;
}
.food-img img{
  width: 80px;
  height: 80px;
}
.food-context{
  -ms-flex: 1;
      flex: 1;
  margin-left: 10px;
  position: relative;
}
.food-context .change-goods-number{
  position: absolute;
  right: 5px;
  bottom: -10px;
}
.change-goods-number .num-span{
  display: inline-block;
  width: 24px;
  text-align: center;
  height: 24px;
  line-height: 24px;
  vertical-align: text-bottom;
}
.change-goods-number .link-change{
  display: inline-block;
  width: 24px;
  height: 24px;
  border: 2px solid rgb(0,160,220);
  border-radius: 100%;
  text-align: center;
  position: relative;
}
.change-goods-number .cut::after{
  content: "-";
  font-size: 24px;
  color: rgb(0,160,220);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.change-goods-number .add{
  background-color: rgb(0,160,220);
}
.change-goods-number .add::after{
  content: "+";
  font-size: 24px;
  color: #fff;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.food-title{
  font-size:18px;
  color: rgb(7,17,27);
  margin-bottom: 8px;
}
.food-description{
  margin-bottom: 8px;
  font-size: 10px;
  color: rgb(147,153,159);
}
.food-price .price{
  font-size: 15px;
  color: rgb(240, 20, 20);
}
.food-price .old-price{
  margin-left: 6px;
  font-size: 12px;
  color: rgb(147,153,159);
  text-decoration: line-through;
}




