
.header {
  position: relative;
  background-color: rgba(7, 17, 27, 0.5);
  width: 100%;
  overflow: hidden;
}
.backgrounp {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: -1;
  filter: blur(10px);
  -webkit-filter: blur(10px);
}
.wapper {
  height: 100%;
  width: 100%;
  padding: 24px 12px 16px 24px;
  position: relative;
}
.seller-count {
  position: absolute;
  height: 15px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 15px;
  right: 50px;
  bottom: 10px;
  color: rgb(255, 255, 255);
  padding: 7px 8px;
  line-height: 15px;
  font-size: 10px;
  font-weight: 200;
}
.wapper .avatar, .content {
  display: inline-block;
}
.avatar {
  width: 64px;
  height: 64px;
  border-radius: 4px;
  vertical-align: top;
}
.content {
  vertical-align: top;
  margin-left: 16px;
}
.title {
  color: #ffffff;
  font-size: 16px;
}
.description {
  color: #ffffff;
  margin-top: 8px;
  font-size: 12px;
}
.support {
  color: #ffffff;
  margin-top: 10px;
  font-size: 10px;
}
.ion-brand {
  display: inline-block;
  width: 30px;
  height: 16px;
  background: url("./brand@2x.png") no-repeat top center;
  background-size: 100% 100%;
  vertical-align: top;
}
.supportIcon {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-size: 100% 100%;
  vertical-align: top;
  background-repeat: no-repeat;
  margin-top: -2px;
}
.decrease {
  background-image: url("./decrease_1@2x.png");
}
.discount {
  background-image: url("./discount_1@2x.png");
}
.special {
  background-image: url("./special_1@2x.png");
}
.invoice {
  background-image: url("./invoice_1@2x.png");
}
.guarantee {
  background: url("./guarantee_3@2x.png") no-repeat;
}
.bulletin-wrapper {
  height: 28px;
  background-color: rgba(7, 17, 27, 0.2);
  line-height: 28px;
  padding: 0 22px 0 12px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 10px;
  color: rgb(255, 255, 255);
  font-weight: 200;
  position: relative;
}
.bulletin-wrapper .bulletin-title {
  display: inline-block;
  width: 22px;
  height: 12px;
  background: url("./bulletin@2x.png") no-repeat;
  background-size: 100% 100%;
  vertical-align: top;
  margin-top: 7px;
}
.bulletin-wrapper .bulletin-text {
  margin: 0 4px;
  vertical-align: top;
}
.bulletin-wrapper .icon-keyboard_arrow_right {
  position: absolute;
  display: inline-block;
  width: 20px;
  height: 20px;
  color: rgb(255, 255, 255);
  font-size: 16px;
  top: 6px;
  right: 8px;
  text-align: center;
}
.detail {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(7, 17, 27, 1.0);
  overflow: auto;
  -webkit-backdrop-filter: blur(10px);
}
.detail-main {
  color: rgb(255, 255, 255);
  padding: 36px 64px;
}
.detail-main .title {
  text-align: center;
  font-size: 16px;
  font-weight: 700;
}
.detail-main .level {
  text-align: center;
  margin-top: 16px;
}
.detail-main .level span {
  margin-left: 10px;
  display: inline-block;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.detail-main .level .on {
  background-image: url("./star24_on@2x.png");
}
.detail-main .level .half {
  background-image: url("./star24_half@2x.png");
}
.detail-main .level .off {
  background-image: url("./star24_off@2x.png");
}
.detail .detail-close {
  position: relative;
  font-size: 32px;
  color: rgba(255, 255, 255, 0.5);
  text-align: center;
  clear: both;
}
.detail .detail-mgs-box {
  margin-top: 28px;
}
.title-box {
  position: relative;
  height: 20px;
  line-height: 20px;
  display: -ms-flexbox;
  display: flex;
}
.title-box .link {
  height: 2px;
  background-color: rgba(255, 255, 255, 0.2);
  -ms-flex: 1;
      flex: 1;
}
.title-box .title-word {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
  width: 80px;
  margin-top: -8px;
}
.support-list {
  line-height: 24px;
}
.support-list .support-item {
  font-size: 12px;
  font-weight: 200;
  color: rgb(255, 255, 255);
}
.support-list .support-item i {
  background-size: 100% 100%;
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: top;
  margin-top: 5px;
}
.support-list .support-item span {
  vertical-align: top;
  display: inline-block;
  margin-left: 6px;
}
.detail-bulletin {
  margin-top: 28px;
}
.detail-bulletin .bulletin-text {
  font-size: 12px;
  font-weight: 200;
  line-height: 24px;
}



