
.tab {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.tab::after {
  position: absolute;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  left: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  content: ' ';
}
.tab::before {
  position: absolute;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  left: 0;
  top: 0;
  height: 1px;
  width: 100%;
  content: ' ';
}
.tab .tab-item {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
}
.tab .tab-item > a {
  display: block;
  font-size: 14px;
  color: rgb(77, 85, 93);
}
.tab .tab-item > .active {
  color: rgb(240, 20, 20);
}



